export const configTab = {
    girls: '',
    front: '',
    Android: 'Android',
    iOS: 'iOS',
    other: ''
}

export const timeFormat = (time) => {
    const timeDiff=(new Date().getTime() - new Date(time).getTime())/3600000;
    if(timeDiff/24 >=1){
        return Math.floor(timeDiff/24) + ' ';
    }else if(timeDiff >1){
        return Math.floor(timeDiff) + ' ';
    }else if(timeDiff < 1){
        return Math.ceil(timeDiff*60) + ' ';
    }       
}


